html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

:root {
  --active-color: #00acdc;
}

body {
  margin: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  line-height: 60px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-right: 243px;
  display: block;
}

.nav {
  display: line;
  text-align: center;
}

.nav__item {
  margin-right: 20px;
  text-decoration: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.is-active:after {
  content: " ";
  background: var(--active-color);
  color: var(--active-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: var(--active-color);
}

.nav__link:hover {
  color: var(--active-color);
  -webkit-text-decoration: line;
  text-decoration: line;
  position: relative;
}
